import debounce from 'lodash/debounce.js';
import htmlRender from './html';
import { downArrowDarkBase64, walletIconDarkBase64 } from './iconsBase64';
import { handleCustomEvent, handleRpc, sendEthereumEvent } from './messageHandler';
import styleRender from './style';
import {
    EntryPosition,
    IframeWalletMessageType,
    MessageType,
    walletIframeId,
    walletIframeMask,
    type ProjectConfig,
    type WalletConfig,
    type WalletCore,
    type WalletOption,
} from './types';
import { isNullish, isServer } from './utils';
import { walletUrl } from './walletUrl';

const fullScreenClass = 'particle-pwe-full-screen-iframe-content';

let isListen = false;

let timer: any = null;

let draggie: any;

export class WalletEntryPlugin {
    static WALLET_BTN_POSITION = 'walletBtnPosition';

    private _walletOptions!: WalletOption;

    private projectConfig: ProjectConfig | undefined;

    private _walletCore: WalletCore | undefined;

    /**
     * wallet is center
     */
    private centerWallet = false;

    walletCreated = false;

    get walletCore() {
        return this._walletCore;
    }

    get walletOptions() {
        return this._walletOptions;
    }

    constructor() {
        if (typeof window !== 'undefined') {
            window.addEventListener('message', (event) => {
                if (event?.data?.name === 'particle-network-wallet') {
                    console.log('handleWalletEvent', event);
                    const type = event?.data?.data?.type;
                    if (type === 'logout') {
                        this.walletEntryDestroy();
                    }
                }
            });

            // @ts-ignore
            window.walletEntryPlugin = this;
        }
    }

    init(projectConfig: ProjectConfig, options: WalletOption = {}) {
        this.projectConfig = projectConfig;
        this._walletOptions = {
            entryPosition: EntryPosition.BR,
            themeType: 'light',
            ...options,
            widgetIntegration: options.widgetIntegration ?? 'modal',
            visible: options.visible ?? true,
        };

        const storedPosition = this.getButtonStorageData().position;
        if (storedPosition && storedPosition !== this._walletOptions.entryPosition) {
            localStorage.removeItem(WalletEntryPlugin.WALLET_BTN_POSITION);
            localStorage.removeItem(WalletEntryPlugin.WALLET_BTN_POSITION + '_window');
            this.setInitialButtonPosition();
        }

        this.centerWallet = Boolean(
            this._walletOptions.entryPosition === EntryPosition.MC || !this._walletOptions.visible
        );

        if (this.centerWallet) {
            this._walletOptions.topMenuType = 'close';
        }

        this.destroy();
        if (typeof window !== 'undefined') {
            window.removeEventListener('message', this.#onMessage);
            window.addEventListener('message', this.#onMessage);
        }
    }

    private setInitialButtonPosition() {
        const position = this._walletOptions.entryPosition;
        const { innerWidth, innerHeight } = window;
        let x, y, direction;

        switch (position) {
            case EntryPosition.BR:
                x = innerWidth;
                y = innerHeight;
                direction = 'right';
                break;
            case EntryPosition.BL:
                x = 0;
                y = innerHeight;
                direction = 'left';
                break;
            case EntryPosition.TR:
                x = innerWidth;
                y = 0;
                direction = 'right';
                break;
            case EntryPosition.TL:
                x = 0;
                y = 0;
                direction = 'left';
                break;
            default:
                x = innerWidth;
                y = innerHeight;
                direction = 'right';
        }

        this.setButtonStorageData({ x, y, direction });
    }

    overrideWalletOption(options: Partial<WalletOption>) {
        if (!this.projectConfig) {
            throw new Error('Please init wallet entry plugin first!');
        }
        this._walletOptions = {
            ...this._walletOptions,
            ...options,
        };

        if (this.walletCore && this.walletCreated) {
            this.walletEntryCreate();
        } else {
            this.walletEntryDestroy();
        }
    }

    setWalletCore(walletCore: WalletCore) {
        if (!this.projectConfig) {
            throw new Error('Please init wallet entry plugin first!');
        }
        if (!walletCore.ethereum && !walletCore.solana) {
            throw new Error('Please provide ethereum or solana wallet!');
        }
        if (this._walletCore) {
            this._walletCore.ethereum?.removeListener?.('accountsChanged', this.#onEthereumAccountsChanged);
            this._walletCore.ethereum?.removeListener?.('chainChanged', this.#onEthereumChainChanged);
        }
        this._walletCore = walletCore;
        if (this._walletCore.ethereum) {
            this._walletCore.ethereum?.removeListener?.('accountsChanged', this.#onEthereumAccountsChanged);
            this._walletCore.ethereum?.on?.('accountsChanged', this.#onEthereumAccountsChanged);

            this._walletCore.ethereum?.removeListener?.('chainChanged', this.#onEthereumChainChanged);
            this._walletCore.ethereum?.on?.('chainChanged', this.#onEthereumChainChanged);
        }
    }

    walletEntryCreate() {
        this.destroy();
        if (this._walletOptions?.widgetIntegration !== 'embedded') {
            this.walletEntryRander();

            if (this._walletOptions.preload) {
                const iframe: HTMLIFrameElement = this.getWalletIFrameImpl();
                const iframeContent = document.querySelector('.particle-pwe-iframe-content') as HTMLDivElement;
                if (!iframeContent?.childElementCount) {
                    iframe.style.width = '100%';
                    iframe.style.height = '100%';
                    iframeContent?.appendChild(iframe);
                }
            }
        }
        this.walletCreated = true;
    }

    walletEntryDestroy() {
        this.destroy();
    }

    destroy() {
        if (isServer()) return;
        draggie?.destroy?.();
        window.removeEventListener('resize', this?.resize?.(), false);
        document.querySelector('.particle-wallet-entry-container')?.remove?.();

        const walletBtn = document.querySelector('.particle-pwe-btn') as HTMLButtonElement;
        walletBtn && walletBtn.removeAttribute('data-position');

        const iframe = document.getElementById(walletIframeId);
        if (iframe) {
            iframe.remove();
        }
        this.walletCreated = false;
    }

    private getWalletUrl(options?: WalletConfig): string {
        if (!this.projectConfig) {
            throw new Error('Please init wallet entry plugin first!');
        }
        const url = walletUrl({
            options,
            projectConfig: this.projectConfig,
            walletOptions: this._walletOptions,
            customEventHandler: this._walletCore?.customEventHandler,
        });
        return url;
    }

    public getWalletIFrame(options?: WalletConfig): HTMLIFrameElement {
        if (this._walletOptions.widgetIntegration !== 'embedded') {
            throw new Error('Only `embedded` mode can control the iframe.');
        }

        return this.getWalletIFrameImpl(options);
    }

    private getWalletIFrameImpl(options?: WalletConfig): HTMLIFrameElement {
        if (!this.projectConfig) {
            throw new Error('Please init wallet entry plugin first!');
        }
        if (!this._walletCore) {
            throw new Error('Please call `setWalletCore` first!');
        }

        let iframe: HTMLIFrameElement | null = document.getElementById(walletIframeId) as HTMLIFrameElement;
        if (!iframe) {
            iframe = this.createIFrame();
        }

        const url = this.getWalletUrl(options);
        if (url != iframe.getAttribute('src')) {
            iframe.src = url;
        }
        return iframe;
    }

    private getWalletIFrameMask() {
        let mask: HTMLDivElement | null = document.getElementById(walletIframeMask) as HTMLDivElement;
        if (!mask) {
            mask = document.createElement('div');
            mask.setAttribute('id', walletIframeMask);
            mask.style.position = 'fixed';
            mask.style.top = '0';
            mask.style.left = '0';
            mask.style.width = '100%';
            mask.style.height = '100%';
            mask.style.zIndex = '1000';
            mask.style.backgroundColor = 'rgba(71, 88, 107, 0.24)';
            mask.style.backdropFilter = 'blur(6px)';
            mask.style.display = 'none';
            document.body.appendChild(mask);
        }
        return mask;
    }

    private createIFrame() {
        let iframe: HTMLIFrameElement | null = document.getElementById(walletIframeId) as HTMLIFrameElement;
        if (!iframe) {
            iframe = document.createElement('iframe');
            iframe.style.width = '100%';
            iframe.style.height = '100%';
            iframe.style.border = 'none';
            iframe.allow = 'camera';
            iframe.id = walletIframeId;
            iframe.src = this.getWalletUrl();
        }

        return iframe;
    }

    /**
     * open modal wallet
     * @param params
     * @param params.windowSize large | small
     * @param params.pathName The path to the web wallet page, which is '/' by default.
     * @param params.query Go to the query parameter of the wallet page
     * @returns
     */
    public openWallet(
        params?: {
            windowSize?: 'large' | 'small';
        } & WalletConfig
    ) {
        console.log('open wallet', this._walletOptions, params);
        if (this._walletOptions?.widgetIntegration === 'embedded') {
            throw new Error('embedded mode not support `openWallet`');
        }

        const walletIcon = document.querySelector('.particle-pwe-wallet-icon') as HTMLDivElement;
        const downArrow = document.querySelector('.particle-pwe-down-arrow');
        const iframeContent = document.querySelector('.particle-pwe-iframe-content') as HTMLDivElement;

        if (params?.windowSize === 'large' || window.screen.width < 600) {
            iframeContent?.classList.add(fullScreenClass);
        }
        const modalBorderRadius =
            this._walletOptions?.customStyle?.[this._walletOptions?.themeType || 'light']?.cardBorderRadius ?? 18;
        if (!isNullish(modalBorderRadius)) {
            iframeContent.style.borderRadius = `${modalBorderRadius}px`;
        }
        iframeContent?.classList.add('particle-pwe-iframe-content-show');

        const uiMode = this._walletOptions?.themeType || 'light';

        if (uiMode == 'dark') {
            iframeContent?.classList.add('particle-pwe-iframe-content-dark');
            iframeContent?.classList.remove('particle-pwe-iframe-content-light');
        } else {
            iframeContent?.classList.add('particle-pwe-iframe-content-light');
            iframeContent?.classList.remove('particle-pwe-iframe-content-dark');
        }

        if (this.centerWallet) {
            iframeContent.classList.add('particle-pwe-iframe-center-content');
        }

        const iframe: HTMLIFrameElement = this.getWalletIFrameImpl(params);
        if (!iframeContent.childElementCount) {
            iframe.style.width = '100%';
            iframe.style.height = '100%';
            iframeContent?.appendChild(iframe);
        }

        walletIcon?.classList.add('particle-pwe-wallet-icon-hide');
        downArrow?.classList.remove('particle-pwe-down-arrow-hide');
        if (this.centerWallet) {
            const iframeMask = this.getWalletIFrameMask();
            iframeMask.style.display = 'block';
        }
        this.updateIframeContentPosition();
    }

    // ----- wallet event

    #onMessage = (event: MessageEvent) => {
        if (event.data?.type === IframeWalletMessageType) {
            if (!this._walletCore) {
                throw new Error('Please call `setWalletCore` first!');
            }
            const messageType = event.data?.message?.messageType;
            if (messageType === MessageType.EthereumRpc || messageType === MessageType.SolanaRpc) {
                handleRpc(event, this._walletCore);
            } else {
                handleCustomEvent(event, this._walletCore.customEventHandler);
            }
        }
    };

    #onEthereumAccountsChanged = (args: unknown) => {
        sendEthereumEvent('accountsChanged', args);
    };

    #onEthereumChainChanged = (args: unknown) => {
        sendEthereumEvent('chainChanged', args);
    };

    // ----  render wallet icon

    private setWalletIcon() {
        const walletIconEl = document.querySelector('.particle-pwe-wallet-icon') as HTMLImageElement;
        const downArrowEl = document.querySelector('.particle-pwe-down-arrow') as HTMLImageElement;

        walletIconEl && walletIconEl.setAttribute('src', walletIconDarkBase64);
        downArrowEl && downArrowEl.setAttribute('src', downArrowDarkBase64);

        const walletBtn = document.querySelector('.particle-pwe-btn') as HTMLButtonElement;
        if (walletBtn && this._walletOptions.visible && this._walletOptions.entryPosition !== EntryPosition.MC) {
            walletBtn.style.opacity = '1';
        }
    }

    private updateIframeContentPosition() {
        const iframeContent = document.querySelector('.particle-pwe-iframe-content') as HTMLDivElement;
        if (!iframeContent || iframeContent?.style?.display === 'none') {
            return;
        }
        const walletBtn = document.querySelector('.particle-pwe-btn') as HTMLButtonElement;

        if (!this._walletOptions.visible) {
            walletBtn.style.display = 'none';
        }

        const walletBtnRect = walletBtn.getBoundingClientRect();
        const iframeContentRect = iframeContent.getBoundingClientRect();
        const windowHeight = window.innerHeight;
        const windowWidth = window.innerWidth;
        const iframeContentHeight = iframeContentRect.height;
        const iframeContentWidth = iframeContentRect.width;
        const walletBtnHeight = walletBtnRect.height;
        // const walletBtnWidth = walletBtnRect.width;
        const walletBtnTop = walletBtnRect.top;
        const walletBtnLeft = walletBtnRect.left;
        const walletBtnBottom = walletBtnRect.bottom;
        const walletBtnRight = walletBtnRect.right;

        if (this.centerWallet) {
            // Screen center
            iframeContent.style.top = `${(windowHeight - iframeContentHeight) / 2}px`;
            iframeContent.style.left = `${(windowWidth - iframeContentWidth) / 2}px`;
        } else if (
            walletBtnBottom + iframeContentHeight + 10 < windowHeight &&
            walletBtnRight + iframeContentWidth + 10 < windowWidth
        ) {
            iframeContent.style.top = walletBtnBottom + 10 + 'px';
            iframeContent.style.left = walletBtnLeft + 'px';
        } else if (
            walletBtnBottom + iframeContentHeight + 10 < windowHeight &&
            walletBtnLeft - iframeContentWidth - 10 > 0
        ) {
            iframeContent.style.top = walletBtnBottom + 10 + 'px';
            iframeContent.style.left = walletBtnRight - iframeContentWidth + 'px';
        } else if (
            walletBtnTop - iframeContentHeight - 10 > 0 &&
            walletBtnRight + iframeContentWidth + 10 < windowWidth
        ) {
            iframeContent.style.top = walletBtnTop - iframeContentHeight - 10 + 'px';
            iframeContent.style.left = walletBtnLeft + 'px';
        } else if (walletBtnTop - iframeContentHeight - 10 > 0 && walletBtnLeft - iframeContentWidth - 10 > 0) {
            iframeContent.style.top = walletBtnTop - iframeContentHeight - 10 + 'px';
            iframeContent.style.left = walletBtnRight - iframeContentWidth + 'px';
        } else if (walletBtnRight + iframeContentWidth + 10 < windowWidth) {
            const top = walletBtnTop + walletBtnHeight / 2 - iframeContentHeight / 2;
            iframeContent.style.top =
                top < 30
                    ? 30 + 'px'
                    : top > windowHeight - iframeContentHeight - 30
                    ? windowHeight - iframeContentHeight - 30 + 'px'
                    : top + 'px';
            iframeContent.style.left = walletBtnRight + 10 + 'px';
        } else if (walletBtnLeft - iframeContentWidth - 10 > 0) {
            const top = walletBtnTop + walletBtnHeight / 2 - iframeContentHeight / 2;
            iframeContent.style.top =
                top < 30
                    ? 30 + 'px'
                    : top > windowHeight - iframeContentHeight - 30
                    ? windowHeight - iframeContentHeight - 30 + 'px'
                    : top + 'px';
            iframeContent.style.left = walletBtnLeft - iframeContentWidth - 10 + 'px';
        } else {
            console.log('no space');
        }
    }

    private setButtonStorageData(data: any) {
        const { innerWidth, innerHeight } = window;
        const position = this._walletOptions.entryPosition || EntryPosition.BR;
        localStorage.setItem(
            WalletEntryPlugin.WALLET_BTN_POSITION,
            `${data.x},${data.y},${data.direction},${position}`
        );
        localStorage.setItem(WalletEntryPlugin.WALLET_BTN_POSITION + '_window', `${innerWidth},${innerHeight}`);
    }

    private getButtonStorageData() {
        const { innerWidth, innerHeight } = window;
        const [x, y, direction, position] =
            localStorage?.getItem(WalletEntryPlugin.WALLET_BTN_POSITION)?.split?.(',') || [];
        let [width, height]: any =
            localStorage.getItem(WalletEntryPlugin.WALLET_BTN_POSITION + '_window')?.split?.(',') || [];
        width = Number(width || 0);
        height = Number(height || 0);
        if (width && height && (width != innerWidth || height != innerHeight)) {
            localStorage.removeItem(WalletEntryPlugin.WALLET_BTN_POSITION);
            localStorage.removeItem(WalletEntryPlugin.WALLET_BTN_POSITION + '_window');
            return this.getInitialPositionData();
        }

        return {
            x: Number(x) || 0,
            y: Number(y) || 0,
            position: position || this._walletOptions?.entryPosition,
            direction: direction || this.getDirectionFromPosition(this._walletOptions?.entryPosition),
        };
    }

    private getInitialPositionData() {
        const position = this._walletOptions?.entryPosition || EntryPosition.BR;
        const { innerWidth, innerHeight } = window;
        let x, y;

        switch (position) {
            case EntryPosition.BR:
                x = innerWidth;
                y = innerHeight;
                break;
            case EntryPosition.BL:
                x = 0;
                y = innerHeight;
                break;
            case EntryPosition.TR:
                x = innerWidth;
                y = 0;
                break;
            case EntryPosition.TL:
                x = 0;
                y = 0;
                break;
            default:
                x = innerWidth;
                y = innerHeight;
        }

        return {
            x,
            y,
            position,
            direction: this.getDirectionFromPosition(position),
        };
    }

    private getDirectionFromPosition(position?: EntryPosition): 'left' | 'right' {
        return position === EntryPosition.BL || position === EntryPosition.TL ? 'left' : 'right';
    }

    private updateWalletBtnPosition(x: number, y: number, type = '') {
        const { width } = document.body.getBoundingClientRect();
        const { direction } = this.getButtonStorageData();
        const isRight = x > width / 2 || (type == 'windowResize' && direction === 'right');
        let left = isRight ? width - 60 : 0;
        let top = y;
        let newDirection = 'left';

        const safeDistance = window.screen.width > 600 ? 30 : 10;

        if (left < safeDistance) {
            left = safeDistance;
            newDirection = 'left';
        } else if (left > width - (60 + safeDistance)) {
            left = width - (60 + safeDistance);
            newDirection = 'right';
        }
        if (top < safeDistance) {
            top = safeDistance;
        } else if (top > window.innerHeight - (60 + safeDistance)) {
            top = window.innerHeight - (60 + safeDistance);
        }

        return {
            left,
            top,
            direction: newDirection,
        };
    }

    public closeWallet() {
        const iframeContent: HTMLDivElement | null = document.querySelector('.particle-pwe-iframe-content');
        iframeContent?.classList.remove('particle-pwe-iframe-content-show');
        iframeContent?.classList.remove(fullScreenClass);
        const walletIcon = document.querySelector('.particle-pwe-wallet-icon') as HTMLDivElement;
        const downArrow = document.querySelector('.particle-pwe-down-arrow');
        walletIcon?.classList.remove('particle-pwe-wallet-icon-hide');
        downArrow?.classList.add('particle-pwe-down-arrow-hide');
        const iframeMask = this.getWalletIFrameMask();
        iframeMask.style.display = 'none';
    }

    private resize() {
        // eslint-disable-next-line @typescript-eslint/no-this-alias
        const that = this;
        return () => {
            if (isServer()) return;
            clearTimeout(timer);

            timer = setTimeout(() => {
                const walletBtn: HTMLDivElement | null = document.querySelector('.particle-pwe-btn');

                const { x: btnX, y: btnY, position } = this.getButtonStorageData();

                let x, y;
                if (btnX && btnY) {
                    x = btnX;
                    y = btnY;
                } else {
                    switch (position) {
                        case EntryPosition.BL:
                            x = 0;
                            y = window.innerHeight;
                            break;
                        case EntryPosition.TL:
                            x = 0;
                            y = 0;
                            break;
                        case EntryPosition.TR:
                            x = window.innerWidth;
                            y = 0;
                            break;
                        case EntryPosition.BR:
                        default:
                            x = window.innerWidth;
                            y = window.innerHeight;
                            break;
                    }
                }

                const { left, top, direction } = that.updateWalletBtnPosition(x, y, 'windowResize');
                if (walletBtn) {
                    walletBtn.style.left = left + 'px';
                    walletBtn.style.top = top + 'px';
                }

                this.setButtonStorageData({
                    x: left,
                    y: top,
                    direction,
                });

                that.updateIframeContentPosition();

                (async () => {
                    let count = 0;
                    do {
                        count++;
                        await this.sleep(100);
                        this.updateIframeContentPosition();
                    } while (count < 15);
                })();
            }, 10);
        };
    }

    private async sleep(ms: number) {
        return new Promise((resolve) => setTimeout(resolve, ms));
    }

    private async walletEntryRander() {
        if (isServer()) return;
        styleRender();
        htmlRender();
        this.setWalletIcon();

        const walletBtn = document.querySelector('.particle-pwe-btn') as HTMLButtonElement;
        // const maxZIndex = Math.max.apply(
        //     null,
        //     Array.from(document.querySelectorAll('*')).map((item) => {
        //         return Number(window.getComputedStyle(item).zIndex) || 0;
        //     })
        // );

        // walletBtn.style.zIndex = maxZIndex + 1;

        const { x: btnX, y: btnY } = this.getButtonStorageData();

        if (btnX && btnY) {
            if ((Number(btnX) || 0) < window.innerWidth && (Number(btnY) || 0) < window.innerHeight) {
                const { left, top } = this.updateWalletBtnPosition(Number(btnX) || 0, Number(btnY) || 0);
                walletBtn.style.left = left + 'px';
                walletBtn.style.top = top + 'px';
                this.updateIframeContentPosition();
            } else {
                const { left, top } = this.updateWalletBtnPosition(window.innerWidth, window.innerHeight);
                walletBtn.style.left = left + 'px';
                walletBtn.style.top = top + 'px';
                this.updateIframeContentPosition();
            }
        } else {
            // default wallet btn position
            const position = this._walletOptions.entryPosition || EntryPosition.BL;
            let top, left;
            if (position === EntryPosition.BR) {
                top = window.innerHeight;
                left = window.innerWidth;
            } else if (position === EntryPosition.TR) {
                top = 0;
                left = window.innerWidth;
            } else if (position === EntryPosition.TL) {
                top = 0;
                left = 0;
            } else {
                top = window.innerHeight;
                left = 0;
            }

            const positionData = this.updateWalletBtnPosition(left, top);
            walletBtn.style.left = positionData.left + 'px';
            walletBtn.style.top = positionData.top + 'px';
            walletBtn.setAttribute('data-position', position?.toLowerCase());
            this.updateIframeContentPosition();
        }

        let isDraggie = false;

        const Module = await import('draggabilly');
        const Draggabilly = Module.default ? Module.default : Module;
        //@ts-ignore
        draggie = new Draggabilly(walletBtn);

        draggie.on('dragStart', () => {
            isDraggie = true;
            walletBtn.style.cursor = 'move';
        });

        draggie.on('dragMove', () => {
            this.updateIframeContentPosition();
        });

        draggie.on('dragEnd', (event: any, pointer: any) => {
            event.stopPropagation();
            walletBtn.style.cursor = 'pointer';
            const { clientX: x, clientY: y } = pointer;
            const { left, top, direction } = this.updateWalletBtnPosition(x, y - 35);
            walletBtn.style.left = left + 'px';
            walletBtn.style.top = top + 'px';
            this.updateIframeContentPosition();

            (async () => {
                let count = 0;
                do {
                    count++;
                    await this.sleep(20);
                    this.updateIframeContentPosition();
                } while (count < 70);
            })();

            this.setButtonStorageData({
                x: left,
                y: top,
                direction,
            });

            setTimeout(() => {
                isDraggie = false;
            }, 50);
        });

        const controlIframe = debounce(() => {
            if (!isDraggie) {
                const iframeContent = document.querySelector('.particle-pwe-iframe-content') as HTMLDivElement;
                if (iframeContent?.classList?.contains?.('particle-pwe-iframe-content-show')) {
                    this.closeWallet();
                } else {
                    this.openWallet();
                }
            }
        }, 30);

        walletBtn.addEventListener('touchend', (event: any) => {
            setTimeout(() => {
                controlIframe();
            });
        });

        walletBtn.addEventListener('click', (event: any) => {
            event?.stopPropagation?.();
            setTimeout(() => {
                controlIframe();
            });
        });

        if (!isListen) {
            isListen = true;

            // @ts-ignore
            window.addEventListener('resize', window.walletEntryPlugin.resize(), false);
            window.addEventListener(
                'message',
                (events: any) => {
                    if ((window as any).walletEntryPlugin?.walletOptions?.widgetIntegration === 'embedded') {
                        return;
                    }
                    if (events?.data?.type === 'PARTICLE_WALLET_RESIZE_IFRAME') {
                        const walletEntryPlugin = (window as any).walletEntryPlugin;
                        const iframeContent = document.querySelector('.particle-pwe-iframe-content') as HTMLDivElement;
                        const classList = iframeContent?.classList;
                        if (classList?.contains?.(fullScreenClass)) {
                            if (!this._walletOptions?.visible || window.screen.width < 600) {
                                walletEntryPlugin.closeWallet();
                            } else {
                                classList?.remove?.(fullScreenClass);
                                walletEntryPlugin.resize();
                                walletEntryPlugin.updateIframeContentPosition();
                            }
                        } else {
                            classList?.add?.(fullScreenClass);
                        }
                    } else if (events?.data?.type === 'PARTICLE_WALLET_CLOSE_IFRAME') {
                        // @ts-ignore
                        window.walletEntryPlugin.closeWallet();
                    }
                },
                false
            );
        }
    }
}
